import ApiRoutes from "@/app/configs/endpoint.config";
import HttpClient from "../index.api";

const baseURL = process.env.NEXT_PUBLIC_API_URL;

class Product extends HttpClient {
  constructor() {
    super(baseURL);
    this._initializeRequestInterceptor();
    this._initializeResponseInterceptor();
  }

  //  Top Picks
  getTopPicks = async () => {
    return this.instance({
      method: ApiRoutes.products.topPicks.Method,
      url: ApiRoutes.products.topPicks.Endpoint,
    });
  };

  //  Product Details
  getProductDetails = async (id) => {
    return this.instance({
      method: ApiRoutes.products.details.Method,
      url: `${ApiRoutes.products.details.Endpoint}/${id}`,
    });
  };
}

const productInstance = new Product();
export default productInstance;
